    click: async function(){
      let abc=(this.$refs.bond.value)
      //let sound = document.getElementById("sound");
      console.log(abc)
      console.log(typeof (abc))
      const url = `https://api.dictionaryapi.dev/api/v2/entries/en/${abc}`;
const options = {
	method: 'GET',
	
};
try {
  const response = await fetch(url, options);

  if (!response.ok) {
    throw new Error('HTTP error, status = ' + response.status);
  }

  const result = await response.json();
  const lot = result[0].meanings[0].definitions[0].definition;
  const bodde = result[0].phonetics[0].audio;
  const speech= result[0].meanings[0].partOfSpeech
  const example=result[0].meanings[0].definitions[0].example;
const word=result[0].word
  this.$refs.titt.innerHTML = `
    <div class="word" >
      
      <span style=" display:inline-block;background-color:none;width:90%;
      font-family: 'Barlow Condensed', sans-serif;color: #2196f3; padding:20px;border:0px black solid;
      ">
      <h1>${word}</h1>
      <h2>${lot}</h2>
      <h3>Part of speech: ${speech}</h3>
      <h3 style='font-style:italic'>Example:-${example}</h3>
      
      
      
      
      <audio controls autoplay> 
        <source src="${bodde}" type="audio/ogg">
        <source src="horse.mp3" type="audio/mpeg">
        Your browser does not support the audio element.
      </audio>
      </span>

     
      
    </div>`;

  console.log(result);
  console.log(response.ok);
} catch (error) {
  
  this.$refs.titt.innerHTML='word not found';
  console.log('not found');
}

